import addDays from "date-fns/addDays";

function incrementDate(dateStringSource, numOfDaysToAdd) {
  return addDays(new Date(dateStringSource), numOfDaysToAdd);
}

const dateToIncrementInput = document.getElementById("dateToIncrement");
const numberOfDaysInput = document.getElementById("numberOfDays");
const log = document.getElementById("result");

dateToIncrementInput.addEventListener("input", updateValue);
numberOfDaysInput.addEventListener("input", updateValue);

function updateValue(e) {
  log.textContent = incrementDate(
    dateToIncrementInput.value,
    numberOfDaysInput.value
  );
}
